/* green box:
rgb(30, 175, 113);
 */

 .container {
   display: flex;
   justify-content: center;
   flex-direction: column;
   align-items: center;
   margin-top: 5vh;
 }

 .card {
  box-shadow: 0 1px 4px rgba(0,0,0,.35);
  border-radius: 10px;
 }

 .green-tick {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: -25px;
  transform: translateX(-50%);
  margin: auto;
}

.success-text {
  width: 215px;
  margin-top: 70px;
  margin-bottom: 15px;
}

 .card-header {
   background-color: rgb(30, 175, 113);
   position: relative;
   border-top-left-radius: 10px;
   border-top-right-radius: 10px;
   text-align: center;
   padding: 0 65px;
}

.card-body {
  text-align: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}


.on-text {
  line-height: 21px;
  font-size: 14px;
  padding-top: 20px;
  color: #585858;
  padding-bottom: 15px;
}

.date-text {
  color: #000;
  line-height: 40px;
  font-size: 27px;
  font-weight: 600;
  padding-top: 10px;
  padding-bottom: 15px;
}

.place-text {
  font-size: 22px;
  line-height: 33px;
  font-weight: 500;
  padding-bottom: 25px;
}

.footer {
  text-align: center;
}

.thanks-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 8px;
  display: block;
  margin-top: 20px;
  color: rgb(88, 88, 88);
}

.screenshot-text {
  font-size: 14px;
  line-height: 27px;
  margin-bottom: 30px;
  display: block;
  margin-top: 10px;
  color: rgb(144, 144, 144);
}


.checkout-btn {
  text-decoration: none;
  color: rgb(0, 112, 224);
  font-size: 18px;
  width: 200px;
  border-radius: 50px;
  border: 1.5px solid rgb(0, 112, 224);
  padding: 12.5px 25px;
}

.checkout-icon {
  display: inline-block;
  height: 25px;
  width: 25px;
  margin-left: 10px;
  vertical-align: middle;
  text-align: center;
}